import React from 'react';
import { Panel, Placeholder, Button, PanelHeader } from '@vkontakte/vkui'
import { Icon56ErrorOutline, Icon28ChevronBack } from '@vkontakte/icons';
const Error = props => (

  <Panel id={props.id}>
    <React.Fragment>
      <PanelHeader separator={false}>Ошибка</PanelHeader>
      <Placeholder
        stretched={true}
        icon={<Icon56ErrorOutline fill="#EF5350" />}
        header={<div style={{ color:'white'}}>Ошибка</div>}
        action={props.data.num_error == 1 ? <Button size="m" style={{ background: 'transparent', color: '#5288C1' }} onClick={props.go} data-to="wheel">Переподключится</Button> : props.data.num_error == 2 ? <Button size="m" style={{ background: 'transparent', color: '#5288C1' }} onClick={props.go} data-type='errjoin' data-to="home">Переподключится</Button> : props.data.num_error == 4 ? <Button size="m" style={{ background: 'transparent', color: '#5288C1' }} onClick={props.go} data-to="cube">Переподключится</Button> : <Button size="m" style={{ background: 'transparent', color: '#5288C1' }} onClick={props.go} data-to="home">Переподключится</Button>}
      >
        {props.data.error_msg}
      </Placeholder>
    </React.Fragment>
  </Panel>
);


export default Error;
