import { Icon48WritebarDone, Icon56ErrorOutline, Icon56Users3Outline } from '@vkontakte/icons';
import bridge from '@vkontakte/vk-bridge';
import { ConfigProvider, AdaptivityProvider, AppRoot, Button, Div, ModalCard, ModalRoot, ScreenSpinner, Snackbar, View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import React, { useEffect, useState, useRef } from 'react';
import io from 'socket.io-client';
import Home from './panels/Home.js';
import './panels/css/global.css';
import Error from './panels/error.js';


const App = () => {
	const adsCooldown = useRef(0);
	const adsCooldownTimer = useRef(null);
	const socket_link = 'https://nueta-production-d002.up.railway.app/'
	const [screen, setscreen] = useState('home')
	const [activeunder, setactiveunder] = useState('home');
	const [loading, setloading] = useState(false)
	const [popout, setpopout] = useState(<ScreenSpinner />)
	const [online, setonline] = useState(0)
	const [datatop, settop] = useState([])
	const [uniquecode, setuniquecode] = useState('')
	const [fetchedUser, setUser] = useState({
		id: 0,
		first_name: '',
		last_name: '',
		photo_url: ''
	})
	const [userdata, setuserdata] = useState({
		balance: 0,
		bonus: 0,
		ban: 0
	})
	const [activemodal, setmodal] = useState(null)
	const [errorscreen, seterrorscreen] = useState({
		'num_error': 0,
		'error_msg': ''
	})
	const [snackbar, setsnackbar] = useState(null)
	const [connection, setconnection] = useState(0)
	const [metop, setmetop] = useState([])

	useEffect(() => {
		const tg = window.Telegram.WebApp;

		if (!tg || !tg.initDataUnsafe || !tg.initDataUnsafe.user) {
			window.location.href = "https://t.me/wave_stars_bot"; // вернём в бота
		}
		if (tg) {
			Telegram.WebApp.expand();
		}
		
	}, []);


	useEffect(async () => {
		async function fetchData() {
			window.addEventListener('popstate', e => e.preventDefault() & menu());
			window.history.pushState({ screen: 'home' }, `home`);
			const tg = window.Telegram.WebApp;
			tg.ready(); // обязательно вызвать
			const unsafeData = tg.initDataUnsafe;
			console.log(unsafeData.user)

			setTimeout(() => {
				setUser(unsafeData.user)
				connect(unsafeData.user)
				setpopout(null)
			}, 2345);

			// Вставь это в useEffect
			const script = document.createElement("script");
			script.src = "//libtl.com/sdk.js";
			script.dataset.zone = "9491456";
			script.dataset.sdk = "show_9491456";
			document.body.appendChild(script);

		};
		fetchData()
	}, [])

	useEffect(async () => {
		async function bridgecon() {
			bridge.subscribe((e) => {
				switch (e.detail.type) {
					case 'VKWebAppViewHide':
						setpopout(<ScreenSpinner size="large" />)
						window.socket.disconnect()
						break;
					case 'VKWebAppViewRestore':
						window.socket.connect()
						setpopout(null)
						break;
					case 'VKWebAppAccessTokenFailed':
						setpopout(null)
						setscreen('home')
						break;
					default:
						break;
				}
			})
		}
		bridgecon()
	}, [screen])


	// Таймер с обновлением snackbar каждую секунду
	useEffect(() => {
		let interval;
		if (adsCooldown > 0) {
			setsnackbar(
				<Snackbar
					style={{ marginBottom: 48 }}
					onClose={() => setsnackbar(null)}
					mode='dark'
					before={<Icon56ErrorOutline fill="#FFA000" />}
				>
					Реклама доступна через {adsCooldown} сек.
				</Snackbar>
			);

			interval = setInterval(() => {
				setAdsCooldown(prev => {
					if (prev <= 1) {
						clearInterval(interval);
						setsnackbar(null);
						return 0;
					}
					return prev - 1;
				});
			}, 1000);
		}
		return () => clearInterval(interval);
	}, [adsCooldown]);


	const connect = (data) => {
		window.socket = io.connect(socket_link, {
			autoConnect: false,
			secure: true,
			query: {
				"uid": data.id,
				"nick": data.first_name + ' ' + data.last_name,
				"photo": data.photo_url,
				"params": window.location.search.substring(1),
				'nowcode': uniquecode
			},
			transports: ['websocket'],
			timeout: 5000,
			reconnection: false, // отключаем автоматические переподключения
		});

		events(window.socket);

		window.socket.on('connect_error', (error) => {
			seterrorscreen({
				'num_error': 2,
				'error_msg': 'Ошибка при подключение к серверу! Попробуйте ещё раз.'
			});
			setscreen('error');
			setmodal(null);
			setpopout(null);
			setconnection(0);
			// здесь не вызываем window.socket.connect() повторно, переподключений не будет
		});

		window.socket.on('connect_timeout', () => {
			seterrorscreen({
				'num_error': 2,
				'error_msg': 'Ошибка при подключение к серверу! Попробуйте ещё раз.'
			});
			setscreen('error');
			setmodal(null);
			setpopout(null);
			setconnection(0);
		});

		window.socket.connect();
	};



	const events = (socket) => {
		socket.on(`connect`, () => {
			setconnection(1)
		})
		socket.on(`disconnect`, (reason) => {
			setmodal(null)
			if (reason === "io client disconnect" || reason === "forced close") {
				window.socket.connect()
			} else {
				seterrorscreen({
					'num_error': 2,
					'error_msg': 'Разорвано соединение с сервером'
				})
				setconnection(0)
				setscreen('error')
				setmodal(null)
			}
		})
		socket.on(`error`, () => {
			setmodal(null)
			seterrorscreen({
				'num_error': 2,
				'error_msg': 'Разорвано соединение с сервером'
			})
			setscreen('error')
		})
		socket.on(`response`, (msg) => {

			if (msg.type == 'errorjoinroom') {
				seterrorscreen({
					'num_error': 2,
					'error_msg': 'Ошибка при подключение к игре'
				})
				setTimeout(() => {
					setwheeldata(msg)
					setpopout(null)
					setmodal(null)
					setscreen('error')
					clearTimeout()
				}, 500)
			}


			if (msg.type == 'online') {
				setonline(msg.count)
			}

			if (msg.type == 'datatop') {

				setmetop(msg.mydata)
				setpopout(null)
				settop(msg.array)
			}
			if (msg.type == 'updatebalance') {
				setuserdata({
					'balance': msg.balance,
					'bonus': msg.bonus
				})
				setloading(0)
			}

			if (msg.type == 'errorads') {
				setmodal('errstars_ads')
			}

			if (msg.type == 'successads') {
				setTimeout(() => {
					setsnackbar(<Snackbar
						style={{ marginBottom: 48 }}
						onClose={() => setsnackbar(null)}
						mode='dark'
						before={<Icon48WritebarDone fill="#8BC34A" />}
					>
						Вам выдан бонус в размере {msg.sum} OC!
					</Snackbar>)
					clearTimeout()
				}, 250)
				setuserdata({
					'balance': msg.balance
				})
			}


			if (msg.type == 'userdata') {
				setuserdata(msg)
				setloading(false)
				if (msg.ban == 0) {
					setpopout(null)
				}
				if (msg.ban == 1) {
					seterrorscreen({
						'num_error': 3,
						'error_msg': 'Сервер временно недоступен. В течение нескольких минут всё должно заработать. Следите за новостями в нашей группе.'
					})
					setpopout(null)
					setconnection(0)
					setscreen('error')
					setmodal(null)
				}
			}
			if (msg.type == 'updateuserdata') {
				setuserdata(msg)
			}
		})
	}

	const changeUnder = (e) => {
		if (userdata.ban == 1) {
			seterrorscreen({
				'num_error': 3,
				'error_msg': 'Сервер временно недоступен. В течение нескольких минут всё должно заработать. Следите за новостями в нашей группе.'
			})
			setconnection(0)
			setpopout(null)
			setscreen('error')
			setmodal(null)
			return
		}
		setsnackbar(null)
		if (e.currentTarget.dataset.story == 'rating') {
			setpopout(<ScreenSpinner size='large' />)
			window.socket.emit(`request`, {
				'type': 'gettop'
			})
		}
		setactiveunder(e.currentTarget.dataset.story)
	}

	const updatehome = () => {
		if (userdata.ban == 1) {
			seterrorscreen({
				'num_error': 3,
				'error_msg': 'Сервер временно недоступен. В течение нескольких минут всё должно заработать. Следите за новостями в нашей группе.'
			})
			setconnection(0)
			setpopout(null)
			setscreen('error')
			setmodal(null)
			return
		}
		setloading(true)
		window.socket.emit(`request`, {
			'type': 'getuserbalance'
		})
	}

	const go = (e) => {
		if (userdata.ban == 1) {
			seterrorscreen({
				'num_error': 3,
				'error_msg': 'Сервер временно недоступен. В течение нескольких минут всё должно заработать. Следите за новостями в нашей группе.'
			})
			setconnection(0)
			setpopout(null)
			setscreen('error')
			setmodal(null)
			return
		}
		window.history.pushState({ panel: e.currentTarget.dataset.to }, `${e.currentTarget.dataset.to}`);
		setscreen(e.currentTarget.dataset.to)
		if (connection == 0) {
			seterrorscreen({
				'num_error': 2,
				'error_msg': 'Разорвано соединение с сервером'
			})
			setTimeout(() => {
				setscreen('error')
				clearTimeout()
			}, 300)
		}
		setsnackbar(null)
	};

	const menu = () => {


		if (userdata.ban == 1) {
			seterrorscreen({
				'num_error': 3,
				'error_msg': 'Сервер временно недоступен. В течение нескольких минут всё должно заработать. Следите за новостями в нашей группе.'
			})
			setconnection(0)
			setpopout(null)
			setscreen('error')
			setmodal(null)
			return
		} else {
			setsnackbar(null)
			window.socket.emit(`request`, {
				'type': 'leavegameroom',
				'room': 'all'
			})
			setmodal(null)
			setscreen('home')
			window.history.pushState({ screen: 'home' }, `home`);
		}
	}


	const videobonus = async () => {
		if (adsCooldown.current > 0) {
			setsnackbar(
				<Snackbar
					style={{ marginBottom: 48 }}
					onClose={() => setsnackbar(null)}
					mode='dark'
					before={<Icon56ErrorOutline fill="#FFA000" />}
				>
					Реклама доступна через {adsCooldown.current} сек.
				</Snackbar>
			);
			return;
		}

		if (!window.show_9491456) {
			setsnackbar(
				<Snackbar
					style={{ marginBottom: 48 }}
					onClose={() => setsnackbar(null)}
					mode='dark'
					before={<Icon56ErrorOutline fill="#FF5C5C" />}
				>
					Реклама временно недоступна, попробуйте позже.
				</Snackbar>
			);
			return;
		}

		try {
			setpopout(<ScreenSpinner size="large" />);
			await window.show_9491456(); // Показываем рекламу

			window.socket.emit('request', {
				type: 'stars_ads',
				data: { result: true },
				prov: uniquecode
			});

			setsnackbar(
				<Snackbar
					style={{ marginBottom: 48 }}
					onClose={() => setsnackbar(null)}
					mode='dark'
					before={<Icon48WritebarDone fill="#4CAF50" />}
				>
					Вам начислено 0.10 звезды!
				</Snackbar>
			);

			// ⏱️ Кулдаун
			adsCooldown.current = 5;
			adsCooldownTimer.current = setInterval(() => {
				adsCooldown.current--;
				if (adsCooldown.current <= 0) {
					clearInterval(adsCooldownTimer.current);
				}
			}, 1000);

		} catch (error) {
			// Если реклама не была просмотрена или возникла ошибка, покажем ошибку:
			setsnackbar(
				<Snackbar
					style={{ marginBottom: 48 }}
					onClose={() => setsnackbar(null)}
					mode='dark'
					before={<Icon56ErrorOutline fill="#FF5C5C" />}
				>
					Реклама временно недоступна, попробуйте позже.
				</Snackbar>
			);

			window.socket.emit('request', {
				type: 'stars_ads',
				data: { result: false },
				prov: uniquecode
			});
		} finally {
			setpopout(null);
		}
	};


	const modal = (
		<ModalRoot activeModal={activemodal}>
			<ModalCard
				id='errads'
				onClose={() => setmodal(null)}
				icon={<Icon56ErrorOutline fill="#FF5C5C" />}
				header="Ошибка"
				subheader="Данная функция доступна только на мобильных устройствах."
				actions={
					<Button size="l" style={{ background: '#3498DB', borderRadius: 2 }} onClick={() => setmodal(null)}>
						Закрыть
					</Button>
				}
			></ModalCard>


			<ModalCard
				id='referral'
				onClose={() => setmodal(null)}
				icon={<Icon56Users3Outline fill="#3498DB" />}
				header="Реферальная ссылка"
				subheader={
					<div style={{ wordBreak: 'break-all', textAlign: 'center' }}>
						Делись ссылкой — за каждого приглашённого ты получишь <b>2 звезды</b> 🌟
					</div>
				}
				actions={
					<div style={{ width: '100%', display: 'flex', justifyContent: 'center' }}>
						<Button
							size="l"
							style={{ background: '#3b121fce', color: 'white', borderRadius: 15 }}
							onClick={(e) => {
								const textArea = document.createElement("textarea");
								textArea.value = 'https://t.me/wave_stars_bot?start=' + fetchedUser.id;
								document.body.appendChild(textArea);
								textArea.select();
								document.execCommand("copy");
								document.body.removeChild(textArea);

								const button = e.target;
								const originalText = button.innerText;
								button.innerText = 'Скопировано!';
								setTimeout(() => {
									button.innerText = originalText;
								}, 2000);
							}}
						>
							Скопировать ссылку
						</Button>
					</div>
				}
			/>


		</ModalRoot>
	);

	return (
		<ConfigProvider platform="ios">
			<AdaptivityProvider>
				<AppRoot>
					<View activePanel={screen} modal={modal} popout={popout}>
						<Home id="home" metop={metop} typemodal={setmodal} videobonus={videobonus} datatop={datatop} online={online} connection={connection} userdata={userdata} go={go} activeunder={activeunder} loading={loading} fetchedUser={fetchedUser} changeUnder={changeUnder} updatehome={updatehome} snackbar={snackbar} />
						<Error id="error" go={go} data={errorscreen} />
					</View>
				</AppRoot>
			</AdaptivityProvider>
		</ConfigProvider>
	);
}


export default App;
